import React, { useEffect, useMemo, useState } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import { Button, IconButton, Tab, Typography, Paper, InputBase } from "@material-ui/core";
import CampaignHeader from '../../components/Common/campaignHeader';
import { StyledTab, StyledTabs } from '../../components/Common/StyledTabs';
import SearchIcon from '@material-ui/icons/Search';
import AvailableTable from '../../components/Builder/AvailableTable';
import SubscribedTable from '../../components/Builder/SubscribedTable';
import { useDispatch, useSelector } from "react-redux";

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        width: '100%',
        height: 'calc(100vh - 103px)',
        backgroundColor: '#332B4F',
        padding: '30px 45px'
    },
    padding: {
        padding: theme.spacing(3),
    },
    header: {
        display: 'flex',
        marginTop: '24px',
        marginLeft: '24px',
        [theme.breakpoints.down('sm')]: {
            display: 'block'
        },
    },
    tabs: {
        display: 'flex',
        justifyContent: 'space-between',
        marginTop: 20
    },
    paper: {
        padding: '2px 5px',
        display: 'flex',
        alignItems: 'center',
        width: 300,
        backgroundColor: '#231C3D',
        border: '1px solid #17122B',
        borderColor: '#17122B',
    },
    iconButton: {
        padding: 10,
        color: 'white'
    },
    input: {
        marginLeft: theme.spacing(1),
        flex: 1,
        color: 'white'
    },
    searchDiv: {
        display: 'flex'
    },
    tabMain: {
    },
    buttons: {
        width: 200,
        height: 50,
        margin: 30
    }
}));

const Builder = (props) => {

    const classes = useStyles();

    const [value, setValue] = React.useState(1);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };
    const anchor = require('@project-serum/anchor');

    const admin = useSelector(state => state.main.admin);
    const customer = useSelector(state => state.main.customer);
    const builder = useSelector(state => state.main.builder);
    const architect = useSelector(state => state.main.architect);
    const program = useSelector(state => state.main.program);
    const pool_flag = useSelector(state => state.main.pool_flag);

    const [active_data, setActiveData] = React.useState([]);
    const [available_data, setAvailableData] = React.useState([]);

    const convertData = (index) => {

        let t_data = available_data[index];
        let temp = [...available_data];
        temp.splice(index, 1);
        let temp1 = [...active_data];
        temp1.push(t_data);
        setAvailableData(temp);
        setActiveData(temp1)
    }
    async function getList() {
        try {
            const pool = await program.account.poolAccount.fetch(admin.publicKey);
            console.log('pool', pool)
            let campaigns = pool.campaigns;

            setActiveData([]);
            setAvailableData([]);
            let temp = [];
            let temp1 = [];

            for (let z = 0; z < pool.head; z++) {
                const campaignAddr = await program.account.campaign.associatedAddress(campaigns[z]);
                console.log("architect ", campaigns[z].toBase58(), "\tcreated campaign ", campaignAddr.toBase58());
                console.log("campaigns ", campaigns[z]);
                const campaign = await program.account.campaign.fetch(campaigns[z].toBase58());
                console.log("campaign ", campaign);
                let data = {
                    publicKey: campaigns[z].toBase58(),
                    domain: new TextDecoder().decode(Uint8Array.from(campaign.domain.filter(item =>
                        item !== 0))),
                    topic: new TextDecoder().decode(Uint8Array.from(campaign.subject.filter(item =>
                        item !== 0))),
                    explain: new TextDecoder().decode(Uint8Array.from(campaign.explain.filter(item =>
                        item !== 0))),
                    phrase: new TextDecoder().decode(Uint8Array.from(campaign.phrase.filter(item =>
                        item !== 0))),
                    status: 'progress',
                    progress: '',
                    remain: campaign.timeLimit.words[campaign.timeLimit.length - 1],
                    architectStakeAmount: campaign.architectStakeAmount.words[campaign.architectStakeAmount.length - 1],
                    minBuilder: campaign.minBuilder.words[campaign.minBuilder.length - 1],
                    minValidator: campaign.minValidator.words[campaign.minValidator.length - 1],
                    validationQuorum: campaign.validationQuorum,
                    penalty: '',
                    apy: ''
                };
                if (data.architectStakeAmount > 0) {
                    temp.push(data);
                } else {
                    temp1.push(data);
                }


            }

            setActiveData(temp);
            setAvailableData(temp1);

        } catch (error) {
            console.log('error', error)
        }
    }
    useEffect(() => {

        if (program != null) {
            getList();
        }
    }, [program])


    useEffect(() => {
        async function createOntology() {

            const stake_amount = new anchor.BN(1000);
            const stake_period = new anchor.BN(7);
            const transactions = await program.rpc.architectInit(
                stake_amount,
                stake_period,
                {
                    accounts: {
                        ontologyAccount: architect.publicKey,
                        architect: architect.publicKey,
                        campaign: customer.publicKey,
                        pool: admin.publicKey,
                    },
                    instructions: [
                        await program.account.ontologyAccount.createInstruction(architect),
                    ],
                    signers: [architect],
                });
        }
        if (pool_flag) {
            console.log('program', program)
            // createOntology();
        }
    }, [pool_flag])

    return (
        <div className={classes.root}>
            <CampaignHeader />
            <div className={classes.tabs}>
                <StyledTabs value={value} onChange={handleChange} aria-label="styled tabs example">
                    <StyledTab label="Subscribed" />
                    <StyledTab label="Available" />
                </StyledTabs>
                <Paper component="form" className={classes.paper}>
                    <InputBase
                        className={classes.input}
                        placeholder="Search..."
                        inputProps={{ 'aria-label': 'search google maps' }}
                    />
                    <IconButton className={classes.iconButton} aria-label="search">
                        <SearchIcon />
                    </IconButton>
                </Paper>
            </div>
            {
                value === 0 ? <SubscribedTable active_data={active_data} />
                    : <AvailableTable available_data={available_data} convertData={convertData} />
            }
        </div>
    )
}

export default Builder;